.bottom_button {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--p);
  cursor: pointer;
  transition: all 0.2s linear;
}
.bottom_button h2 {
  color: #fff;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  transition: all 0.3s linear;
}
.bottom_button:hover {
  background-color: var(--s);
}
.bottom_button:active h2 {
  transform: scale(0.9);
}
