.container {
  opacity: 0;
}
.container p {
  font-size: 1.2rem;
  font-weight: 600;
  line-height: 1.6;
  margin-bottom: 1rem;
}
.container .list_item {
  margin-bottom: 0.5rem;
}
.container p:last-child {
  margin-bottom: 0rem;
}
.container p span {
  color: var(--p);
}
.container p .hred {
  color: red;
}
.container p .mega_highlight {
  width: 100%;
  display: inline-block;
  padding: 1rem;
  background-color: #ff1f1f;
  color: #fff;
  border-radius: 0.5rem;
}
.headline {
  padding-bottom: 2rem;
}
.headline h1 {
  text-align: center;
  color: var(--p);
}
.image_container,
.single_image,
.single_image_full {
  width: 100%;
  margin: 2rem 0rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.image_container .img {
  width: 480px;
  flex-shrink: 0;
}
.single_image .img {
  width: 840px;
}
.single_image_full .img {
  width: 100%;
}
.image_container .img img,
.single_image .img img,
.single_image_full .img img {
  width: 100%;
  border-radius: 0.5rem;
}
.image_container .img1 {
  margin-right: 2rem;
}
@media screen and (max-width: 1120px) {
  .image_container {
    flex-direction: column;
  }
  .image_container .img1 {
    margin-right: 0rem;
    margin-bottom: 2rem;
  }
}
@media screen and (max-width: 968px) {
  .single_image .img {
    width: 100%;
  }
}
@media screen and (max-width: 608px) {
  .image_container .img {
    width: 100%;
  }
}
@media screen and (max-width: 480px) {
  .image_container,
  .single_image {
    margin: 1rem 0rem;
  }
}
.fadeInNext {
  animation: fadeInNext 0.2s linear 0s 1 normal forwards;
}
@keyframes fadeInNext {
  0% {
    opacity: 0;
    transform: translateX(5%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.fadeInPrev {
  animation: fadeInPrev 0.2s linear 0s 1 normal forwards;
}
@keyframes fadeInPrev {
  0% {
    opacity: 0;
    transform: translateX(-5%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
