.modal {
  width: 100%;
  min-height: 100%;
  background-color: #fff;
  z-index: 1;
  border-radius: 14px;
  box-shadow: 2px 2px 15px rgba(0, 114, 203, 0.1);
  overflow: hidden;
  transform: scale(0);
  animation: open_animation 0.15s ease-in-out 0s 1 forwards;
}
@keyframes open_animation {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
.container {
  width: 100%;
}
.logo {
  padding: 1rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.content {
  padding: 0rem 2rem;
}
.next_previous {
  width: 100%;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.next_previous button {
  padding: 0.8rem 1.8rem;
  cursor: pointer;
  border: none;
  outline: none;
  font-size: 1.2rem;
  font-weight: 600;
  color: #fff;
  border-radius: 4px;
  transition: all 0.2s linear;
}
.next_previous button:first-child {
  background-color: var(--s);
  margin-right: 2rem;
}
.next_previous button:last-child {
  background-color: var(--p);
}
.next_previous button:first-child:hover {
  background-color: #fff;
  color: var(--s);
  box-shadow: inset 0px 0px 0px 2px var(--s);
}
.next_previous button:last-child:hover {
  background-color: #fff;
  color: var(--p);
  box-shadow: inset 0px 0px 0px 2px var(--p);
}
.next_previous button:active {
  transform: scale(1.05);
}
@media screen and (max-width: 480px) {
  .content {
    padding: 0rem 1rem;
  }
  .logo {
    margin-bottom: 1rem;
  }
  .next_previous {
    padding: 1rem;
  }
}
