.box {
  width: 100%;
  min-height: 100%;
  background-color: #fff;
  z-index: 1;
  border-radius: 12px;
  box-shadow: 2px 2px 15px rgba(0, 114, 203, 0.1);
  overflow: hidden;
  display: flex;
}

.inner_container {
  width: 100%;
  padding: 3rem;
  padding-bottom: calc(80px + 3rem);
  flex-shrink: 0;
  position: relative;
  transition: all 0.3s linear;
}

@media screen and (min-width: 768px) {
  .box {
    width: 600px;
    min-height: 80vh;
  }
}
