.container {
  width: 100%;
  min-height: 100vh;
  padding: 2rem;
  background-image: url("../../assets/identity/icon-logo.png");
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fafafa;
  opacity: 0.855;
}

@media screen and (max-width: 480px) {
  .container {
    padding: 1.5rem;
  }
}
