.form_header {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 1.5rem;
}
.form_header img {
  margin-bottom: 1rem;
}
.form_header h2 {
  color: var(--p);
  text-align: center;
  font-size: 18px;
}
.input_container {
  width: 100%;
  position: relative;
  height: 60px;
  overflow: hidden;
  margin-bottom: 1rem;
  border-radius: 3px;
}
.input_container:last-child {
  margin-bottom: 0rem;
}
.input_container input {
  width: 100%;
  height: 100%;
  color: #fff;
  padding: 20px 20px 0px 20px;
  border: none;
  background-color: var(--p);
  font-size: 1rem;
}
.input_container label {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-bottom: 1px solid white;
}
.input_container label::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0px;
  width: 100%;
  height: 100%;
  border-bottom: 3px solid var(--p_c);
  transform: translateX(-100%);
  transition: all 0.3s ease;
}
.content_name {
  position: absolute;
  bottom: 0px;
  left: 20px;
  padding-bottom: 20px;
  color: #fff;
  transition: all 0.3s ease;
}
.input_container input:focus {
  outline: none;
}
.input_container input:focus + .label_name .content_name,
.input_container input:valid + .label_name .content_name {
  transform: translateY(-45%);
  font-size: 13px;
  left: 10px;
  color: var(--p_c);
}
.input_container input:focus + .label_name::after,
.input_container input:valid + .label_name::after {
  transform: translateX(0%);
}
.i_c_hide {
  transform: translateX(-100%);
  opacity: 0;
}
.i_c_open {
  transform: translateX(-100%);
  opacity: 1;
}
