.question {
  width: 100%;
  text-align: center;
  color: var(--p);
}
.line {
  width: 100%;
  padding: 1rem 0rem 1.7rem 0rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.line span {
  width: 50%;
  height: 2px;
  background-color: var(--s);
}
.options {
  display: flex;
  flex-direction: column;
}
.options label {
  margin-bottom: 1rem;
  padding: 12px;
  background-color: var(--s);
  color: #fff;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 17px;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}
.options label:last-child {
  margin-bottom: 0rem;
}
.options label p {
  display: inline-block;
}
.options label input {
  position: absolute;
  top: -100%;
  left: -100%;
}
.options .active {
  animation: active_animation 0.15s ease-in-out 0s 1 forwards;
}
.options .active p {
  transition: all 0.15s ease-in-out;
}
@keyframes active_animation {
  0% {
    border: none;
    background-color: var(--s);
    color: #fff;
  }
  100% {
    border: 1px solid var(--p);
    background-color: #fff;
    color: var(--p);
  }
}
.hide_box {
  animation: hide_animation 0.15s ease-in-out 0s 1 forwards;
}
@keyframes hide_animation {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
